import React from 'react'
import { Card, CardBody } from 'reactstrap'
import Chart from 'react-apexcharts'
import { FormattedMessage } from 'react-intl'
import liability from '../../assets/img/avatar/card4.png'

class DashboardCards extends React.Component {
  render() {
    let abc = 'linear-gradient(to right, #56ccf2, #2f80ed)'
    if (this.props.cardn === 1) {
      abc = 'linear-gradient(to right, #6441a5, #2a0845)'
    } else if (this.props.cardn === 2) {
      abc = 'linear-gradient(to right, #56ccf2, #2f80ed)'
    } else if (this.props.cardn === 3) {
      abc = 'linear-gradient(to right,#525252, #3d72b4)'
    } else if (this.props.cardn === 4) {
      abc = 'linear-gradient(to right, #348f50, #56b4d3)'
    } else if (this.props.cardn === 5) {
      abc = 'linear-gradient(to right, #f2994a, #f2c94c)'
    } else if (this.props.cardn === 6) {
      abc = 'linear-gradient(to right, #bc4e9c, #f80759)'
    } else if (this.props.cardn === 7) {
      abc = 'linear-gradient(to right, #636363, #a2ab58)'
    } else if (this.props.cardn === 8) {
      abc = 'linear-gradient(to right,#000000, #434343)'
    }
    console.log('ttttttttt', this.props)
    return (
      <Card
        style={{
          //backgroundImage: this.props.cardb ? `url(${this.props.cardb})` : null,
          //background: '#283c86',  /* fallback for old browsers */
          //background: '-webkit-linear-gradient(to right, #bdc3c7, #2c3e50)',  /* Chrome 10-25, Safari 5.1-6 */
          //background: 'linear-gradient(to right, #56ccf2, #2f80ed)', /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

          background: abc,
          //backgroundPosition: 'center',
          //backgroundSize: 'cover',
          //backgroundRepeat: 'no-repeat',
          width: window.screen.width < 500 ? '140px' : '300',
          minHeight: '170px',
          marginLeft: '5px',
          marginRight: '5px',
          margin: '15px 5px',
          boxShadow:
            '0 0 10px 0 rgb(110, 110, 110), 0 5px 5px 0 rgb(117, 117, 117)'
        }}
      >
        <CardBody
          className={`${
            this.props.className ? this.props.className : 'stats-card-body'
          } d-flex ${
            !this.props.iconRight && !this.props.hideChart
              ? 'flex-column align-items-start'
              : this.props.iconRight
              ? 'justify-content-between flex-row-reverse align-items-center'
              : this.props.hideChart && !this.props.iconRight
              ? 'justify-content-center flex-column text-center'
              : null
          } ${!this.props.hideChart ? 'pb-0' : 'pb-2'} pt-2`}
        >
          <div className="icon-section">
            <div
              className={`avatar avatar-stats p-50 m-0 ${
                this.props.iconBg
                  ? `bg-rgba-${this.props.iconBg}`
                  : 'bg-rgba-primary'
              }`}
            >
              <div className="avatar-content">{this.props.icon}</div>
            </div>
          </div>

          <div className="title-section">
            <h2
              style={{
                //color: this.props.iconBg,
                color: 'white',
                fontSize: window.screen.width < 500 ? '10px' : '26px',
                fontWeight: 'bolder'
              }}
              className="text-bold-900 mt-1 mb-25"
            >
              <FormattedMessage id={this.props.stat} />
            </h2>
            {window.screen.width > 500 && (
              <p
                style={{
                  //color: this.props.iconBg,
                  color: 'white'
                }}
                className="mb-0"
              >
                <FormattedMessage id={this.props.statTitle} />
              </p>
            )}
          </div>
        </CardBody>
        {!this.props.hideChart && (
          <Chart
            options={this.props.options}
            series={this.props.series}
            type={this.props.type}
            height={this.props.height ? this.props.height : 100}
          />
        )}
      </Card>
    )
  }
}
export default DashboardCards
